import { Component, Prop, h } from '@stencil/core';

@Component({
	tag: 'ks-notification', styleUrl: 'notification.css'
})
export class Alert {
    @Prop({reflect: true}) red = false;
    @Prop({reflect: true}) icon = "";

	render() {
        if(this.icon) return [
            <div class="icon"><ks-icon name={this.icon}/></div>,
            <div class="content"><slot/></div>
        ];

		return <slot/>;
    }
}